<script>
import Snap from 'snapsvg'

export default {
  props: {
    deg: {
      type: Number,
      default: 0
    }
  },
  data: function () {
    return {
      isFadeinLeaves: false,
      isFadeoutLeaves: false,
      isDoingAnimation: false,
      isAnimatedSVG1: false,
      svg1: undefined
    }
  },
  watch: {
    deg() {
      this.draw(this.deg)
    }
  },
  methods: {
    draw(deg) {
      console.log(deg, this.isDoingAnimation, this.isAnimatedSVG1)
      if (deg > 5) {
        if (!this.isDoingAnimation && !this.isAnimatedSVG1) {
          this.infAnim(this.svg1, 'up')
          this.isDoingAnimation = true
          this.isAnimatedSVG1 = true
        }
      } else {
        if (!this.isDoingAnimation && this.isAnimatedSVG1) {
          this.infAnim(this.svg1, 'down')
          this.isDoingAnimation = true
          this.isAnimatedSVG1 = false
        }
      }
    },
    infAnim(el, dir) {
      // MEMO: thisがvueを指していないので対策
      var _this = this
      var pathFrom = 'M338,1078l-2-2a11.64,11.64,0,0,1-2-3l-2-4c-1-2-2-5-3-7a54.15,54.15,0,0,0-3-6c-2-3-1.14-2.14-2-3s0,0-2-3c-1.24-1.86-1-4-4-11-1.64-3.82-3.86-8.3-8-8-4,.29-7,8-8,10s-1,9-1,11c0,2.91,1,10,1,11,0,2,0,0,1,4a32.53,32.53,0,0,0,1,6c2,6,1.55,5.11,2,6,2,4,2,3,3,5,.63,1.26,1,3,2,4a12.38,12.38,0,0,1,2,3,9.67,9.67,0,0,0,2,3,32.62,32.62,0,0,1,3,3Z'
      var pathMid = 'M361.57,1060.45C355,1062,347,1057,343,1053c-2-2-8.67-9.18-11-13-6.5-10.63-6-11-9-15s-3-7-5-10c-1.75-2.63-3.59-10.17-5-13-2-4-4-14-6-19-1.49-3.71-4-14-5-24-1.71-17.14,2.81-35.58-1-47-3-9-9-13-19-12-7,.7-14,12-14,18,0,2.37-2,41-2,45,0,2.91.42,25.14,1,28,.15.73,3.76,20.29,4,21,1,3,0,3,1,6,1.3,3.91,3,8,4,11l4,12c1,3,4,9,5,12a155.86,155.86,0,0,0,9,15c3.23,4.85,5,8,9,13,5.37,6.72,2,1,13.77,16.15Z'
      var pathTo = 'M360.57,1060.45c-11.17-.3-14.16,2.83-21-6s-16.49-25.74-24-34c-8.95-9.85-15-59.19-15-75-1.13-20.52,16.34-54,33-66s24-19,38-40.35c3.95-6,4.1-8.73,0-14.65-3.95-5.73-17.52-4.29-23,0-16.26,12.7-43.82,26.1-52.65,44.74-1.78,3.75-4.14,8.16-8.29,8.47-4,.29-6.92-3.4-9.27-6.6-21.19-28.87-55.18-49.1-91-49.72-2.92-.06-6.93,1.27-6.35,4.12a5.05,5.05,0,0,0,1.07,1.95l16.15,21.25c5,6.61,15.94,11.47,22.32,16.79,5.73,4.77,23.92,17.67,30,22,14.86,10.55,16,34,16,59,0,0,1.23,59.43,2.23,70.15,1.77,18.88,7.87,37.88,20.46,52.06,8.64,9.74,20.19,17.11,26.51,28.49Z'
      var mid, end, fadeinORfadeout
      if (dir === 'up') {
        // start = pathFrom
        mid = pathMid
        end = pathTo
        fadeinORfadeout = 'fadein'
        dir = 'down'
      } else {
        // start = pathTo
        mid = pathMid
        end = pathFrom
        fadeinORfadeout = 'fadeout'
        dir = 'up'
      }
      if (fadeinORfadeout === 'fadeout') {
        this.isFadeinLeaves = false
        this.isFadeoutLeaves = true
      }
      el.animate({d: mid}, 1500, function() {
        el.animate({d: end}, 1000, function() {
          // disp leaves
          // this対策
          if (fadeinORfadeout === 'fadein') {
            _this.isFadeinLeaves = true
            _this.isFadeoutLeaves = false
          }
          _this.isDoingAnimation = false
        })
      })
    }
  },
  mounted() {
    this.draw(this.deg)
    this.svg1 = window.Snap.select('#me_after_path')
  }
}
</script>

<style scoped>
.cls-1{fill:#f9ebb4;}
.cls-2{fill:#8a0003;}
.cls-3{fill:#3bdb00;}
.cls-4{fill:#72303a;}
.cls-5{fill:#422500;opacity:0.56;}
.cls-5,.cls-6{stroke:#ec0005;stroke-miterlimit:10;}
.cls-6{fill:none;stroke-width:5px;}

#leaf {
  opacity: 0;
}
.fadein {
  animation: fadeIn 1s ease 0s 1 forwards;
}
.fadeout {
  animation: fadeOut 500ms ease 0s 1 forwards;
}
@keyframes fadeIn {
    0% {opacity: 0}
    100% {opacity: 1}
}
@keyframes fadeOut {
    0% {opacity: 1}
    100% {opacity: 0}
}
</style>



